.box-enter {
  opacity: 0;
  transform: translateX(-100%);
}

.box-enter-active {
  opacity: 1;
  transform: translateX(0%);
  transition: opacity 300ms, transform 300ms;
}

.box-exit {
  opacity: 1;
  transform: translateX(0%);
}

.box-exit-active {
  opacity: 0;
  transform: translateX(100%);
  transition: opacity 300ms, transform 300ms;
}

