@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(40deg);
  }
}

@keyframes clouds-loop-1 {
  100% {
    background-position: -2532px;
  }
  0% {
    background-position: 0;
  }
}

@keyframes World {

  100% {

  opacity: 1;
  }
  
  0% {

    opacity: 0;
  }
}

@keyframes fadeAnimation {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
  
